.content__input {
  position: relative;
}

/* width 따로 props로 조저예정 */
.content__input .result-list {
  position: absolute;
  visibility: visible;
  top: 39px;
  left: 0px;
  overflow: scroll;
  width: 193px;
  max-height: 160px;
  padding: 10px 0;
  border: solid 2px #cbddfc;
  background-color: #ffffff;
  color: #6e757c;
  opacity: 1;
  transition: 0.5s;
}
.content__input .result-list::-webkit-scrollbar-track {
  background-color: transparent;
}
/* :focus로 해결해도 될듯 */
.content__input .result-list--hidden {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
}

.content__input .result-list__item {
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 0 20px;
  width: 100%;
  border-bottom: solid 1px #ced4da;
  line-height: 35px;
}

.content__input .result-list__item:hover {
  cursor: pointer;
  background-color: #f0f0f0;
}

.content__input .result-list__item--last {
  border: none;
}

